.button {
  position: relative;
  margin: 10px;
  overflow: hidden;
  cursor: pointer;
}
.button::after {
  display: block;
  content: "";
  position: absolute;

  left: -130%;
  top: 0;
  height: 100%;
  width: 20%;
  transform: skew(45deg);
  background-color: #ffffff;
  opacity: 0.7;
  z-index: 14;
}

.red{
  background: red;
}
.default{
  background: chartreuse;
}
.secondary{
  background: chocolate;
}
.disabled{
  background: seagreen;
}